@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  top: 10%;
}

body {
  background-color: #ffffff;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.App button {
  margin: 10px;
}

* {
  font-family: "Roboto";
  font-weight: 800;
  color: #000000;
}

p {
  margin-top: 30px;
}

.container1 {
  margin: 5% auto auto 0;
}

.container2 {
  margin: 0 auto;
}

.chart {
  display: inline-block;
}

.canvas-container {
  display: inline-block;
}

.loading {
  display: inline-block;
  width: 222px;
}

.icon {
  font-size: 40px;
}

.spinner {
  margin: 5px auto 0;
  width: 70px;
  text-align: center;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #ffa800;

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
}

:root {
  --primary: #39a9db;
  --warning: #f9dc5c;
  --danger: #e84855;
  --success: #1ebf71;
  --white: #fdfdfd;
  --dark: #181717;
}

.btn {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
  /* top: 50%;
  left: 50%; */
  margin: 10% auto 3% auto;
}

.btn:hover {
  /* transform: translateY(-3px); */
  -webkit-transform: scale(1.05); /*Grows in size like Angry Birds button*/
  -moz-transform: scale(1.05);
  -ms-transform: scale(1.05);
  -o-transform: scale(1.05);
}

/* Button Colors and Styles */

.btn--primary--solid {
  background-color: #39a9db;
  background-color: var(--primary);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--warning--solid {
  background-color: #f9dc5c;
  background-color: var(--warning);
  color: #181717;
  color: var(--dark);
  border: none;
}

.btn--danger--solid {
  background-color: #e84855;
  background-color: var(--danger);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--success--solid {
  background-color: #1ebf71;
  background-color: var(--success);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--primary--outline {
  background-color: transparent;
  border: 2px solid #39a9db;
  border: 2px solid var(--primary);
  color: #39a9db;
  color: var(--primary);
}

.btn--warning--outline {
  background-color: transparent;
  border: 2px solid #f9dc5c;
  border: 2px solid var(--warning);
  color: #f9dc5c;
  color: var(--warning);
}

.btn--danger--outline {
  background-color: transparent;
  border: 2px solid #e84855;
  border: 2px solid var(--danger);
  color: #e84855;
  color: var(--danger);
}

.btn--success--outline {
  background-color: transparent;
  border: 2px solid #1ebf71;
  border: 2px solid var(--success);
  color: #1ebf71;
  color: var(--success);
}

/* Button Size */

.btn--medium {
  padding: 10px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 15px 30px;
  font-size: 20px;
}

